<template>
  <div :class="classes">
    <dl>
      <dt><slot name="img"></slot></dt>
      <dd><slot></slot></dd>
    </dl>
  </div>
</template>

<script>
  const prefixCls = 'gtu-products-section3-item'

  export default {
    name: 'products_third_section_item',
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section3-item';
  $circleRadius: 50px;

  #{$prefixCls} {
    margin-top: 60px;
    dt {
      width: $circleRadius*2;
      height: $circleRadius*2;
      margin-bottom: 20px;
      border-radius: $circleRadius*2;
      background-color: #fff;
    }
    dd {
      text-align: center;
    }
  }
</style>


