<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
  const prefixCls = 'gtu-products-section4-item'

  export default {
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section4-item';

  #{$prefixCls} {
    padding: 10px;
    img {
      max-width: 100%;
    }
  }
</style>


