<template>
  <!-- 产品 -->
  <div :class="classes">
    <!-- BEGIN:banner -->
    <Banner :bg="bannerBg">
      <template slot="header">百万数据  极速渲染</template>
      <template>数据上图功能支持百万级数据量 多种渲染方式快速制图 <br /> 点密度、图标、等级、符号、聚合、热度、图表、灯光</template>
    </Banner>
    <!-- END: banner -->
    <!-- BEGIN: 第一部分 -->
    <FirstSection></FirstSection>
    <!-- END: 第一部分 -->
    <!-- BEGIN: 第二部分 -->
    <SecondSection></SecondSection>
    <!-- END: 第二部分 -->
    <!-- BEGIN: 第叁部分 -->
    <ThirdSection></ThirdSection>
    <!-- END: 第叁部分 -->
    <!-- BEGIN: 第四部分 -->
    <FourthSection></FourthSection>
    <!-- END: 第四部分 -->
    <!-- BEGIN: 第五部分 -->
    <FifthSection></FifthSection>
    <!-- END: 第五部分 -->
    <!-- BEGIN: 第六部分 -->
    <SixthSection></SixthSection>
    <!-- END: 第六部分 -->
    <!-- BEGIN: 第七部分 -->
    <SeventhSection></SeventhSection>
    <!-- END: 第七部分 -->
  </div>
</template>

<script>
  import {imgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-products'

  export default {
    name: 'products',
    components: {
      Banner: () => import(/* webpackChunkName: "common" */ '@/components/banner'),
      FirstSection: () => import(/* webpackChunkName: "products" */ '@/views/products/FirstSection'),
      SecondSection: () => import(/* webpackChunkName: "products" */ '@/views/products/SecondSection'),
      ThirdSection: () => import(/* webpackChunkName: "products" */ '@/views/products/ThirdSection'),
      FourthSection: () => import(/* webpackChunkName: "products" */ '@/views/products/FourthSection'),
      FifthSection: () => import(/* webpackChunkName: "products" */ '@/views/products/FifthSection'),
      SixthSection: () => import(/* webpackChunkName: "products" */ '@/views/products/SixthSection'),
      SeventhSection: () => import(/* webpackChunkName: "products" */ '@/views/products/SeventhSection')
    },
    data () {
      return {
        bannerBg: `${imgDir}/products_banner.jpg`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>


