<template>
  <iSection :class="classes">
    <!-- HEAD -->
    <div slot="head" class="gtu-container">
      <SectionTitle>
        <template slot="title">轻松连接您的数据</template>
        <template slot="desc">平台支持50多类开放数据，覆盖吃喝住行、人口经济，助力地理大数据分析</template>
      </SectionTitle>
    </div>
    <!-- CONTENT -->
    <div :class="contentWrapClasses">
      <SectionItem  v-for="(s, i) in sourceLists" :key="i">
        <img slot="img" :src="s.imgUrl" />
        {{s.text}}
      </SectionItem>
    </div>
    
  </iSection>
</template>
<script>
  const prefixCls = 'gtu-products-section3'

  export default {
    name: 'products_third_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle'),
      SectionItem: () => import(/* webpackChunkName:"products" */'@/views/products/ThirdSectionItem.vue')
    },
    data () {
      return {
        sourceLists: [
          {text: '本地文件', imgUrl: ''},
          {text: 'PostgreSQL', imgUrl: ''},
          {text: 'FTP', imgUrl: ''}
        ]
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls : ".gtu-products-section3";
  $imgWidth: 100px;

  #{$prefixCls} {
    background-color: #354140;
    color: #ddd;
    .gtu-products-section-head-desc {
      color: #ccc;
    }
    &-content-wrap {
      width: ($imgWidth + 60)*3;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
    }
  }
</style>


