<template>
  <iSection>
    <!-- HEAD -->
    <div slot="head" class="gtu-container">
      <SectionTitle>
        <template slot="title">一览无余 输出高清地图</template>
        <template slot="desc">支持高清地图打印幅面高度A0尺寸，地图细节纤毫毕现</template>
      </SectionTitle>
    </div>
    <!-- CONTENT -->
    <div :class="contentWrapClasses">
      <img :src="imgUrl" />
    </div>
  </iSection>
</template>

<script>
  import {productsImgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-products-section1'

  export default {
    name: 'products_first_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle')
    },
    data () {
      return {
        imgUrl: `${productsImgDir}/section_1.png`
      }
    },
    computed: {
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section1';

  #{$prefixCls} {
    &-content-wrap {
      text-align: center;
      img {
        max-width: 100%;
      }
    }
  }
</style>


