<template>
  <iSection :class="classes">
    <div slot="head" :class="headWrapClasses">
      <SectionTitle>
        <template slot="title">快速分享，便捷开发；用地图传递信息</template>
        <template slot="desc">地图H5一键式发布 微信、微博、腾讯空间等多平台轻松分享</template>
      </SectionTitle>
    </div>
    <div :class="contentWrapClasses">
      <img :src="imgUrl" />
    </div>
  </iSection>
</template>

<script>
  import {productsImgDir} from '@/utils/staticUrl'

  const prefixCls = 'gtu-products-section5'

  export default {
    name: 'products_fifth_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle')
    },
    data () {
      return {
        imgUrl: `${productsImgDir}/section_5.jpeg`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      headWrapClasses () {
        return [`${prefixCls}-head-wrap`]
      },
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section5';

  #{$prefixCls} {
    position: relative;
    &-head-wrap {
      position: absolute;
      top: 100px;
      width: 100%;
    }
  }
</style>


