<template>
  <section :class="classes">
    <slot name="head"></slot>
    <div :class="contentClass">
      <slot></slot>
    </div>
  </section>
</template>

<script>
  const prefixCls = 'gtu-products-section'

  export default {
    name: 'products_section',
    data () {
      return {
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      contentClass () {
        return [`${prefixCls}-content`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-products-section";
  $sectionPadding: 76px;

  #{$prefixCls} {
    padding: $sectionPadding 0;
    font-size: 14px;
    &-content {
      overflow: hidden;
    }
  }
</style>


