<template>
  <iSection :class="classes">
    <div slot="head" :class="headWrapClasses">
      <SectionTitle>
        <template slot="title">为移动端而生的交互地图</template>
        <template slot="desc">极小的文件包，低俗网络也能快速加载，流畅交互</template>
      </SectionTitle>
    </div>
    <div :class="contentWrapClasses">
      <img :src="imgUrl" />
    </div>
  </iSection>
</template>

<script>
  import {productsImgDir} from '@/utils/staticUrl'

  const prefixCls = 'gtu-products-section6'

  export default {
    name: 'products_sixth_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle')
    },
    data () {
      return {
        imgUrl: `${productsImgDir}/section_6.jpeg`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      headWrapClasses () {
        return [`${prefixCls}-head-wrap`]
      },
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section6';

  #{$prefixCls} {
    position: relative;
    &-head-wrap {
      position: absolute;
      right: 16%;
      top: 50%;
      margin-top: -80px;
    }
  }
</style>


