<template>
  <iSection :class="classes">
    <div slot="head" :class="headWrapClasses">
      <SectionTitle>
        <template slot="title">50+类 千万级开发数据</template>
        <template slot="desc">内置多类开放护具，覆盖吃喝住行、人口经济，助力地理大数据分析</template>
      </SectionTitle>
    </div>
    <div :class="contentWrapClasses">
      <img :src="imgUrl" />
    </div>
  </iSection>
</template>

<script>
  import {productsImgDir} from '@/utils/staticUrl'

  const prefixCls = 'gtu-products-section2'

  export default {
    name: 'products_second_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle')
    },
    data () {
      return {
        imgUrl: `${productsImgDir}/section_2.png`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`, 'gtu-container']
      },
      headWrapClasses () {
        return [`${prefixCls}-head-wrap`]
      },
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section2';

  #{$prefixCls} {
    position: relative;
    &-head-wrap {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -80px;
    }
  }
</style>


