<template>
  <div :class="headClasses">
    <div :class="headTitleClasses">
      <slot name="title"></slot>
    </div>
    <p :class="headDescClasses">
      <slot name="desc"></slot>
    </p>
  </div>
</template>

<script>
  const prefixCls = 'gtu-products-section'

  export default {
    name: 'products_section_title',
    computed: {
      headClasses () {
        return [`${prefixCls}-head`]
      },
      headTitleClasses () {
        return [`${prefixCls}-head-title`]
      },
      headDescClasses () {
        return [`${prefixCls}-head-desc`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-products-section";

  #{$prefixCls} {
    &-head {
      margin-bottom: 10px;
      text-align: center;
      &-title {
        margin-bottom: 15px;
        font: {
          size: 26px;
          weight: normal;
        }
      }
      &-desc {
        color: #666;
        font: {
          size: 16px;
          weight: 300;
        }
      }
    }
  }
</style>


