<template>
  <iSection class="gtu-container">
    <!-- HEAD -->
    <div slot="head">
      <SectionTitle>
        <template slot="title">多样化底图提供</template>
        <template slot="desc">40+地图数据符号与蚊子样式标准定制，界线、植被、河流、铁路、道路、城市轨道、岛屿、海洋</template>
      </SectionTitle>
    </div>
    <!-- CONTENT -->
    <div :class="contentWrapClasses">
      <SectionItem v-for="(e, i) in exampleLists" :key="i">
        <img :src="e.imgUrl" />
      </SectionItem>
    </div>
    
  </iSection>
</template>
<script>
  import {productsImgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-products-section4'

  export default {
    name: 'products_third_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle'),
      SectionItem: () => import(/* webpackChunkName:"products" */'@/views/products/FourthSectionItem.vue')
    },
    data () {
      return {
        exampleLists: [
          {link: '', imgUrl: `${productsImgDir}/section_4_1.jpeg`},
          {link: '', imgUrl: `${productsImgDir}/section_4_2.jpeg`},
          {link: '', imgUrl: `${productsImgDir}/section_4_3.jpeg`},
          {link: '', imgUrl: `${productsImgDir}/section_4_4.jpeg`}
        ]
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls : ".gtu-products-section4";

  #{$prefixCls} {
    &-content-wrap {
      display: flex;
      justify-content: space-around;
      align-items: flex-end;
      &>* {
        flex-basis: 25%;
      }
    }
  }


  
</style>


