<template>
  <iSection class="gtu-container">
    <!-- HEAD -->
    <div slot="head">
      <SectionTitle>
        <template slot="title">行业大数据模型</template>
        <template slot="desc">通过人工智能和机器学习的方法，在地图上分析和解决业务中的实际问题，直击企业的经营痛点</template>
      </SectionTitle>
    </div>
    <!-- CONTENT -->
    <div :class="contentWrapClasses">
      <img :src="imgUrl" />
    </div>
  </iSection>
</template>

<script>
  import {productsImgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-products-section7'

  export default {
    name: 'products_seventh_section',
    components: {
      iSection: () => import(/* webpackChunkName:"products" */'@/views/products/Section'),
      SectionTitle: () => import(/* webpackChunkName:"products" */'@/views/products/SectionTitle')
    },
    data () {
      return {
        imgUrl: `${productsImgDir}/section_7.png`
      }
    },
    computed: {
      contentWrapClasses () {
        return [`${prefixCls}-content-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-products-section7';

  #{$prefixCls} {
    &-content-wrap {
      text-align: center;
      img {
        max-width: 100%;
      }
    }
  }
</style>


